.button {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  font-size: rem(14);
  line-height: 1.5;
  color: #121212;
  min-width: rem(260);
  padding: rem(14) rem(10);
  background-color: $yellow;
  text-transform: uppercase;
  transition: all 0.3s;

  @media (min-width:$mobile) {
    min-width: rem(240);
  }

  &::before {
    margin-left: rem(28);
  }

  @media (any-hover: hover) {
    &:hover {
      background-color: $yellowLight;
    }
  }

  &:active {
    background-color: $yellowLight;
  }

  &--secondary {
    background-color: transparent;
    border: rem(1) solid $turquoise;
    transition: all 0.3s;

    @media (any-hover: hover) {
      &:hover {
        color: #fff;
        background-color: $turquoise;
      }
    }
  }

  &--benefits {
    @media (min-width:$mobile) {
      background-color: transparent;
      border: rem(1) solid $turquoise;
    }

    @media (any-hover: hover) {
      &:hover {
        @media (min-width:$mobile) {
          color: $white;
          background-color: $turquoise;
        }
      }
    }

    &:active {
      @media (min-width:$mobile) {
        color: $white;
        background-color: $turquoise;
      }
    }
  }

  &--blue {
    border: none;
    color: $white;
    background-color: $turquoise;
    transition: background-color 0.3s;

    @media (any-hover: hover) {
      &:hover {
        background-color: $turquoiseLight;
      }
    }
  }

  &--checkout {
    background-color: transparent;
    border: 1px solid #e0e0e0;
    font-weight: 700;
    transition: all 0.3s;

    @media (any-hover: hover) {
      &:hover {
        color: $error;
        border: 1px solid $error;
        background-color: transparent;
      }
    }
  }

  &--order {
    background-color: transparent;
    border: 1px solid #bdbdbd;
    font-weight: 700;
    transition: all 0.3s;

    @media (any-hover: hover) {
      &:hover {
        color: $white;
        border: 1px solid transparent;
        background-color: $turquoiseLight;
      }
    }
  }
}

.modal-close {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  @include square(rem(47));
  border-radius: 50%;
  background-color: $grey;
  transition: background-color 0.3s;

  &::before,
  &::after {
    content: "";
    position: absolute;
    width: rem(14);
    height: rem(3);
    background-color: #f2f2f2;
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }

  @media (any-hover: hover) {
    &:hover {
      background-color: rgba(51, 51, 51, 0.9);
    }
  }
}
