.benefits {
  overflow: hidden;

  @media (min-width:$tablet) {
    display: flex;
    flex-direction: row-reverse;
    max-width: rem(1440);
    margin: 0 auto;
  }

  &__image-wrapper {

    @media (min-width:$tablet) {
      flex: 0 0 calc(((100vw - 60px) * 0.4) + 30px);
      transform: translateX(100%);
      padding: rem(18) 0;
      @include adaptiveValue("padding-left", 50, 0, 1440, 1230, 0);
      max-width: rem(633);
    }

    @media (min-width:em(1230.98)) {
      // flex: 0 0 calc(100vw - (math.div(
      //  (100vw - $containerWidth), 2)
      // ) + (#{$containerWidth * 0.4}) + 30px);
      flex: 0 0 calc(50vw - (0.4 - 0.5) * #{$maxWidthContainer});
    }
  }

  &__image {
    @media (min-width:$tablet) {
      padding-bottom: percent(792, 583);
    }

    @include hoverImg;
  }

  &__content {
    flex: 0 0 100%;
  }

  &__container {
    position: relative;

    @media (min-width:$tablet) {
      display: flex;
      justify-content: flex-end;
    }
  }

  &__prev {
    display: none;

    @media (min-width:$mobile) {
      display: inline-flex;
      right: 0;
      top: 0;
    }

    @media (min-width:$tablet) {
      display: none;
    }
  }

  &__body {
    display: flex;
    flex-direction: column;
    gap: rem(30) rem(20);
    @include adaptiveValue("padding-top", 192, 100, 1230, 768, 1);
    @include adaptiveValue("padding-bottom", 152, 100, 1230, 768, 1);
    padding-left: 0;

    @media (min-width:$mobile) {
      flex-direction: row;
      align-items: center;
    }

    @media (min-width:$tablet) {
      flex-direction: column;
      align-items: stretch;
      width: 61.5%;
      padding-left: rem(20);
    }
  }

  &__list {
    display: inline-flex;
    flex-direction: column;
    flex-wrap: wrap;
    gap: rem(20) rem(40);
    @include adaptiveValue("margin-bottom", 60, 0, 1230, 991, 0);

    @media (min-width:$tablet) {
      flex-direction: row;
    }

    @media (max-width:$tablet) {
      flex: 0 1 50%;
    }
  }

  &__action {
    @media (max-width:$tablet) {
      flex: 0 1 50%;
    }
  }

  &__title {
    display: flex;
    @include adaptiveValue("font-size", 36, 32, 768, 320, 1);
    color: $grey;
    font-weight: 700;
    line-height: 1.5;
    margin-bottom: rem(4);

    &::before {
      font-size: rem(10);
      @include adaptiveValue("margin-right", 10, 8);
      transform: translateY(rem(20));
    }
  }

  &__text {
    @include adaptiveValue("padding-left", 20, 18);
    color: $colorLabel;
    line-height: 1.5;
  }

  &__button {
    @include adaptiveValue("margin-bottom", 70, 36, 1230, 991, 1);

    @media (min-width:$mobile) {
      background-color: transparent;
      border: rem(1) solid $turquoise;
      transition: all 0.3s;

      @media (any-hover: hover) {
        &:hover {
          color: #fff;
          background-color: $turquoise;
        }
      }
    }
  }

  &__discounts {
    @include adaptiveValue("padding-left", 100, 0, 1230, 991, 0);
    font-size: rem(24);
    font-family: $fontSecond;
    letter-spacing: rem(3.72);
    text-transform: uppercase;
    color: $turquoise;

    & span {
      display: block;
      margin-bottom: rem(4);
      color: $error;
      font-size: rem(36);
      line-height: 1.5;
      letter-spacing: rem(5.58);
    }
  }
}
