.for-whom {

  &__body {
    position: relative;
  }

  &__title {
    color: $grey;
    font-size: rem(36);
    font-family: $fontSecond;
    line-height: 1.5;
    letter-spacing: rem(5.58);
    text-transform: uppercase;
    margin-bottom: rem(26);

    @media (min-width:$fromMobile) {
      margin-bottom: rem(42);
    }

    @media (min-width:$tablet) {
      margin-bottom: rem(34);
    }
  }

  &__cards {
    @include grid;
    @include adaptiveValue("row-gap", 80, 40);
  }

  &__prev {
    bottom: 0;
    right: rem(-30);

    @media (min-width: em(1024)) {
      top: 0;
      right: 0;
    }
  }

  &__next {
    bottom: 0;
    left: 0;
    transform: rotate(180deg);

    @media (max-width:em(1023)) {
      display: none;
    }
  }
}

.card-robust {
  grid-column: 1 / -1;
  display: flex;
  align-items: center;
  column-gap: rem(20);

  &__image-wrapper {
    display: none;

    @media (min-width:$mobile) {
      display: block;
      flex: 0 1 50%;
      @include adaptiveValue("padding-right", 85, 0, 1230, 768, 0);
    }

    @media (max-width:$fromMobile) {
      flex: 0 0 284px;
    }
  }

  &__image {
    padding-bottom: 100%;

    @include hoverImg;
  }

  &__content {
    flex-basis: 100%;

    @media (min-width:$fromMobile) {
      flex: 0 1 50%;
    }
  }
}

.card-beautiful {
  grid-column: 1 / -1;

  @media (min-width:$mobile) {
    display: flex;
    flex-direction: row-reverse;
    column-gap: rem(20);
  }

  @media (min-width:$tablet) {
    grid-column: 1 / 7;
    flex-direction: column;
    @include adaptiveValue("row-gap", 44, 0, 1230, 992, 0);
    @include adaptiveValue("padding-left", 102, 0, 1230, 992, 1);
  }

  &__image-wrapper {
    display: none;

    @media (min-width:$mobile) {
      display: block;
      @include adaptiveValue("padding-top", 120, 0, 1230, 768, 0);
      @include adaptiveValue("padding-right", 172, 0, 1230, 768, 0);
      @include adaptiveValue("padding-left", 50, 44, 991.98, 768, 0);
      flex: 0 1 50%;
    }
  }

  &__image {
    padding-bottom: percent(417, 300);

    @include hoverImg;
  }

  &__content {
    flex-basis: 100%;

    @media (min-width:$mobile) {
      flex: 0 1 50%;
    }
  }
}

.card-savetime {
  grid-column: 1 / -1;

  @media (min-width:$mobile) {
    display: flex;
    column-gap: rem(20);
    @include adaptiveValue("padding-left", 100, 0, 1230, 992, 0);
  }

  @media (min-width:$tablet) {
    grid-column: 7 / -1;
    flex-direction: column;
    @include adaptiveValue("row-gap", 44, 0, 1230, 992, 0);
  }

  &__image-wrapper {
    display: none;

    @media (min-width:$mobile) {
      display: block;

      flex: 1 1 percent(404, 284);
    }
  }

  &__image {
    padding-bottom: percent(670, 470);

    @include hoverImg;
  }

  &__content {
    flex-basis: 100%;

    @media (min-width:$mobile) {
      flex: 1 0 rem(396);
    }
  }
}

.type-card {
  display: none;

  @media (min-width:$mobile) {
    display: flex;
    align-items: center;
    @include adaptiveValue("column-gap", 30, 10, 1230, 768, 0);
    @include adaptiveValue("padding-left", 70, 30, 1230, 768, 0);
    color: $grey;
    line-height: 1.5;

    &:not(:last-child) {
      @include adaptiveValue("margin-bottom", 40, 24, 1230, 768, 0);
    }

    &::before {
      font-size: rem(8);
    }

    &--savetime,
    &--beautiful {
      @media (min-width:em(1024)) {
        position: relative;
        left: percent(-44, 586);
      }
    }

    &--beautiful {
      padding-left: 0;
      text-align: right;

      @media (max-width:$tablet) {
        justify-content: flex-end;
      }
    }

    &--savetime {
      @media (min-width:$tablet) {
        padding-left: 0;
      }
    }
  }
}

.description {

  @media (min-width:$mobile) {
    display: flex;
    align-items: baseline;
    @include adaptiveValue("column-gap", 68, 16, 1230, 768, 0);
  }

  &--beautiful {
    @media (min-width:$mobile) {
      flex-direction: column;
      row-gap: rem(22);
    }
  }

  &__num {
    position: relative;
    flex-shrink: 0;
    margin-left: rem(4);

    @media (max-width:$mobile) {
      @include adaptiveValue("margin-bottom", 30, 10, 768, 320, 1);
    }

    &::before {
      content: "";
      display: inline-block;
      position: relative;
      bottom: rem(-2);
      width: rem(1);
      height: rem(46);
      margin-right: rem(6);
      background-color: $grey;
      transform: rotate(22deg);
    }
  }

  &__content {

    @media (max-width:$toMobile) {
      max-width: rem(340);
      margin: 0 auto;
    }
  }

  &__num,
  &__title {
    color: #000;
    font-size: rem(36);
    font-family: $fontSecond;
    letter-spacing: rem(5.58);
    text-transform: uppercase;
  }

  &__title {
    line-height: 1.5;

    &:not(:last-child) {
      margin-bottom: rem(10);
    }

    & span {
      display: none;

      @media (min-width:$mobile) {
        display: inline-block;
        position: relative;

        &::after {
          content: "";
          position: absolute;
          bottom: 0;
          right: rem(-10);
          width: rem(1);
          height: rem(46);
          background-color: $grey;
          transform: rotate(22deg);
        }
      }
    }
  }

  &__text {
    font-size: rem(16);
    line-height: 1.5;
    color: $colorText;
  }
}
