.shopping-bag {
  position: fixed;
  z-index: 7;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s;

  &__wrapper {
    min-height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__body {
    background-color: $white;
    position: relative;
    width: 100%;
    max-width: rem(830);
    max-height: 100%;
    padding-top: rem(10);
    @include adaptiveValue("padding-bottom", 50, 40);
    @include adaptiveValue("padding-left", 30, 14);
    @include adaptiveValue("padding-right", 30, 14);
    opacity: 0;
    transform: perspective(rem(600)) translate(0, -100%) rotate(45deg);
    transition: all 0.8s;

    @media (max-width:em(890)) {
      width: 96%;
    }
  }

  &._modal-open {
    opacity: 1;
    visibility: visible;

    .shopping-bag__body {
      opacity: 1;
      transform: perspective(0) translate(0, 0) rotate(0deg);
    }
  }

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: rem(54);
    @include adaptiveValue("padding-bottom", 34, 0);

    @media (min-width:$mobile) {
      padding-top: rem(34);
    }
  }

  &__button-close {
    position: absolute;
    @include adaptiveValue("top", 30, 10);
    @include adaptiveValue("right", 40, 10);
  }

  &__table-header,
  &__row {
    border-bottom: rem(1) solid #e0e0e0;
  }

  &__table-header {
    padding: rem(10) 0;
  }

  &__row {
    display: none;

    @media (min-width:$mobile) {
      display: flex;
      padding: rem(24) 0;

      &:not(:last-child) {
        margin-bottom: rem(30);
      }
    }
  }

  &__bottom {
    @include adaptiveValue("margin-top", 30, 24);
    padding-right: 0;

    @media (min-width:$mobile) {
      display: flex;
      flex-direction: row-reverse;
      padding-right: rem(34);
    }
  }

  &__amount {
    flex: 1 1 auto;

    @media (max-width:$mobile) {
      margin-bottom: rem(40);
    }
  }

  &__wrapper-button {
    flex: 0 1 rem(500);
  }

  &__button-checkout {
    margin: 0 auto;
  }
}

.table-header {
  display: flex;

  @media (min-width:$mobile) {
    padding-right: rem(34);
  }

  &__lable {
    color: #000;
    line-height: 1.5;
    padding-right: rem(10);

    &--items {
      flex: 0 1 rem(260);

      @media (max-width:$mobile) {
        flex: 1 1 50%;
      }

      @media (min-width:$mobile) {
        text-align: center;
      }
    }

    &--quantity {
      flex: 0 1 rem(240);
      padding-left: rem(40);

      @media (max-width:$mobile) {
        flex: 1 1 50%;
        text-align: center;
        padding-left: 0;
      }
    }

    &--price-currency {
      display: none;

      @media (min-width:$mobile) {
        display: block;
        flex: 1 1 rem(100);
      }
    }

    &--price-total {
      display: none;

      @media (min-width:$mobile) {
        display: block;
        flex: 1 1 rem(126);
      }
    }
  }
}

.product {
  display: flex;
  align-items: center;

  &__info {
    flex: 0 1 rem(260);
    display: flex;
    align-items: center;
    column-gap: rem(30);
  }

  &__image {
    display: none;

    @media (min-width:$mobile) {
      display: block;
      @include square(rem(70));
      border-radius: 50%;

      @include hoverImg;
    }
  }

  &__counter {
    flex: 0 1 rem(240);
    display: flex;
    align-items: center;
    column-gap: rem(20);
    padding: 0 rem(6);

    @media (max-width:$mobile) {
      @include adaptiveValue("column-gap", 40, 20, 767.98, 320, 1);
      margin: 0 auto;
    }
  }

  &__control {
    @extend %button-control;

    &--plus {
      &::after {
        transform: rotate(90deg);
      }
    }
  }

  &__current {
    font-size: rem(12);
    font-weight: 700;

    @media (min-width:$mobile) {
      display: flex;
      align-items: center;
      column-gap: rem(4);
    }
  }

  &__current-text {
    display: none;

    @media (min-width:$mobile) {
      display: block;
    }
  }

  &__price-unit {
    flex: 1 0 rem(100);
  }

  &__price-total {
    flex: 1 0 rem(100);
  }

  &__reset {
    display: none;
    @extend %button-control;

    &::before {
      transform: rotate(45deg);
    }

    &::after {
      transform: rotate(-45deg);
    }

    @media (min-width:$mobile) {
      flex: 0 0 rem(34);
      display: flex;
    }
  }
}

.product-mobile {
  display: none;

  @media (max-width:$mobile) {
    display: block;
    border-bottom: rem(1) solid #e0e0e0;
    padding: rem(26) 0;
  }

  &__row {
    @media (max-width:$mobile) {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      align-items: center;
      column-gap: rem(20);

      &:not(:last-child) {
        margin-bottom: em(24, 16);
      }
    }

    @media (min-width:$mobile) {
      display: none;
    }
  }
}

.amount-shopping {
  flex: 1 0 rem(200);
  display: flex;
  font-size: rem(20);
  color: #000;
  line-height: 1.5;

  &__lable {
    flex: 0 1 50%;

    @media (max-width:$mobile) {
      flex: 0 1 50%;
    }
  }

  &__price {
    flex: 0 1 50%;

    @media (max-width:$mobile) {
      flex: 0 1 50%;
    }
  }
}
