.features {

  &__title {
    @include adaptiveValue("margin-bottom", 70, 22);
    font-size: rem(58);
    font-family: $fontSecond;
    letter-spacing: rem(8);
    color: $turquoise;
    text-transform: uppercase;

    & span {
      @extend %text-outline;
    }
  }

  &__cards {
    @include grid;
    row-gap: rem(50);
  }
}

.card-features {
  grid-column: span 2;

  @media (min-width:$tablet) {
    grid-column: span 4;
  }

  &__image--ibg {
    height: 0;
    padding-bottom: 100%;
    @include adaptiveValue("margin-bottom", 20, 16);

    @media (min-width:$mobileSmall) {
      padding-bottom: 40%;
    }

    @media (min-width:$mobile) {
      padding-bottom: 100%;
    }

    @include hoverImg;
  }

  &__content {
    text-align: center;
  }

  &__title {
    color: $grey;
    font-size: 24px;
    font-family: $fontSecond;
    letter-spacing: rem(3.72);
    text-transform: uppercase;
    @include adaptiveValue("margin-bottom", 20, 6);
  }

  &__text {
    color: $colorText;
    line-height: 1.5;
  }
}
