.potr-team {
  overflow: hidden;

  @media (max-width:$mobile) {
    margin-bottom: rem(110);
  }

  &__body {
    position: relative;

    @media (min-width:em(768)) {
      @include container;
    }
  }

  &__title {
    font-size: rem(58);
    font-family: $fontSecond;
    letter-spacing: rem(8.99);
    @include adaptiveValue("margin-bottom", 82, 26);

    @media (max-width:$mobile) {
      padding: 0 rem(30);
    }
  }

  &__content {
    position: relative;
    @include grid;

    @media (min-width:$mobile) {
      padding-top: rem(114);
      @include adaptiveValue("padding-bottom", 206, 154, 1230, 768, 0);
    }

    @media (min-width:$tablet) {
      padding-top: rem(60);
    }
  }

  &__name {
    display: none;
    @extend %text-outline;

    @media (min-width:em(768)) {
      display: block;
      position: absolute;
      z-index: 1;
      font-family: $fontSecond;
      color: $turquoise;
      @include adaptiveValue("font-size", 288, 220);
      @include adaptiveValue("letter-spacing", 44, 0);

      &--martin {
        top: -3%;
        left: -6%;
      }

      &--andy {
        bottom: -5%;
        right: -3%;
      }
    }
  }

  &__text {
    grid-column: span 2;
    color: $colorText;
    line-height: 1.5;
    background-image: url("../images/potr-team/bg.png");
    background-repeat: no-repeat;

    @media (max-width:$mobile) {
      padding: 0 rem(30) rem(60) rem(30);
      order: 2;
      background-image: url("../images/potr-team/bg-m.png");
      background-position: 0 bottom;
    }

    @media (min-width:$mobile) {
      grid-column: span 3;
      @include adaptiveValue("padding-top", 226, 50, 1230, 768, 0);
      @include adaptiveValue("padding-right", 64, 20, 1230, 768, 0);
    }

    @media (min-width:$tablet) {
      grid-column: span 4;
    }
  }

  &__image-wrapper {
    grid-column: span 2;

    @media (max-width:$mobile) {
      order: 1;
      margin-bottom: rem(30);
    }

    @media (min-width:$mobile) {
      grid-column: span 3;
    }

    @media (min-width:$tablet) {
      grid-column: span 7;
    }
  }

  &__image--ibg {
    height: 0;
    padding-bottom: percent(506, 670);

    @include hoverImg;
  }
}
