.tips-card {
  padding: 0 rem(44);
  line-height: 1.5;
  color: $colorBg;

  @media (min-width:$mobile) {
    display: none;
    padding: rem(34);
  }
}

.materials {
  background-image: url("../images/materials/bg-d.jpg");
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: contain;
  max-width: rem(1440);
  margin: 0 auto;

  @media (max-width:$mobile) {
    background-position: center 35%;
  }

  &__container {
    position: relative;
  }

  &__next,
  &__prev {
    display: none;

    @media (min-width:$mobile) {
      display: block;
      transform: rotate(-180deg);

      &::after {
        margin-top: rem(10);
      }

      @media (any-hover: hover) {
        &:hover {
          &::after {
            transform: translateY(rem(20));
          }
        }
      }
    }

    @media (min-width:$tablet) {
      display: none;
    }
  }

  &__next {
    top: rem(204);
    left: 0;
  }

  &__prev {
    top: 0;
    right: 0;
  }

  &__body {
    position: relative;
  }

  &__content {
    @include grid;
    @include adaptiveValue("padding-bottom", 906, 198, 1440, 320, 1);
  }

  &__title {
    grid-column: span 2;
    color: $turquoise;
    font-family: $fontSecond;
    @include adaptiveValue("letter-spacing", 16, 9);
    font-size: rem(58);
    @include adaptiveValue("font-size", 80, 58, 767.98, 320, 1);

    @media (min-width:$mobile) {
      grid-column: span 6;
      @include adaptiveValue("font-size", 138, 80, 1230, 768, 0);
    }

    @media (min-width:$tablet) {
      grid-column: span 12;
    }
  }

  &__description {
    display: none;

    @media (min-width:$mobile) {
      display: block;
      position: relative;
      grid-column: 4 / -1;
      margin-top: rem(-40);
    }

    @media (min-width:$tablet) {
      grid-column: 8 / -1;
    }

    @media (min-width:em(1024)) {
      margin-top: rem(-130);
    }
  }

  &__subtitle {
    font-size: rem(20);
    font-weight: 700;
    line-height: 1.5;
    color: $colorText;
    @include adaptiveValue("margin-bottom", 16, 10, 1230, 768, 0);
  }

  &__text {
    color: $colorText;
    line-height: 1.5;

    @media (max-width:$tablet) {
      & span {
        font-weight: 700;
      }
    }
  }

  &__tips-cards {
    display: none;
  }
}

.tip-button {
  display: none;

  @media (min-width:$mobile) {
    position: absolute;
    z-index: 2;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    @include square(51px);
    background-color: $error;
    border-radius: 50%;
    transition: background-color 0.3s;
    cursor: pointer;

    &::before,
    &::after {
      content: "";
      position: absolute;
      width: rem(10);
      height: rem(1);
      background-color: #e0e0e0;
    }

    &::after {
      transform: rotate(-90deg);
      transition: all 0.3s;
    }

    @media (any-hover: hover) {
      &:hover {
        background-color: rgba(36, 35, 35, 0.75);

        &::after {
          opacity: 0;
          visibility: hidden;
        }
      }
    }

    &[aria-describedby^="tippy"] {
      background-color: rgba(36, 35, 35, 0.75);

      &::after {
        opacity: 0;
        visibility: hidden;
      }
    }
  }

  &--1 {

    @media (min-width:$mobile) {
      left: percent(49, 708);
      bottom: percent(102, 816);
    }

    @media (min-width:$tablet) {
      left: percent(119, 1170);
      bottom: percent(156, 1242);
    }
  }

  &--2 {

    @media (min-width:$mobile) {
      left: percent(197, 708);
      bottom: percent(230, 816);
    }

    @media (min-width:$tablet) {
      left: percent(412, 1170);
      bottom: percent(400, 1242);
    }
  }

  &--3 {

    @media (min-width:$mobile) {
      left: percent(304, 708);
      bottom: percent(72, 816);
    }

    @media (min-width:$tablet) {
      left: percent(549, 1170);
      bottom: percent(105, 1242);
    }
  }

  &--4 {

    @media (min-width:$mobile) {
      right: percent(189, 708);
      bottom: percent(180, 816);
    }

    @media (min-width:$tablet) {
      right: percent(419, 1170);
      bottom: percent(272, 1242);
    }
  }

  &--5 {

    @media (min-width:$mobile) {
      right: percent(128, 708);
      bottom: percent(318, 816);
    }

    @media (min-width:$tablet) {
      right: percent(143, 1170);
      bottom: percent(373, 1242);
    }
  }
}

.materials-slider {
  display: none;

  @media (max-width:$mobile) {
    width: percent(302, 320);
    padding-top: 28px;
    max-width: rem(450);
    height: 100%;
    transform: translateY(percent(-54, 254));
    background-color: rgba(0, 0, 0, 0.41);

    & .swiper-wrapper {
      position: relative;
      padding: rem(28) 0 rem(16);
    }
  }

  & .materials-bullets {
    display: none;

    @media (max-width:$mobile) {
      display: flex;
      justify-content: center;
      align-items: center;
      column-gap: rem(7);
      padding-bottom: 30px;

      & .swiper-pagination-bullet {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        @include square(rem(12));
        opacity: 1;
        background-color: transparent;

        &::after {
          content: "";
          align-self: center;
          position: absolute;
          @include square(rem(8));
          border-radius: 0;
          background-color: $white;
          transform: rotate(-45deg);
        }
      }

      & .swiper-pagination-bullet-active {
        &::before {
          content: "";
          display: inline-block;
          // position: absolute;
          z-index: 2;
          @include square(rem(6));
          background-color: #000;
          transform: rotate(-45deg);
        }
      }
    }
  }
}
