.questions {
  position: relative;

  &__body {
    @include grid;
    position: relative;

    padding: rem(46) 0 rem(100);
  }

  &__column {

    &--form {
      grid-column: span 2;

      @media (max-width:$mobile) {
        margin-bottom: rem(56);
      }

      @media (min-width:$mobile) {
        grid-column: span 3;
      }

      @media (min-width:$tablet) {
        grid-column: span 4;
      }
    }

    &--our-contacts {
      grid-column: 1 / -1;

      @media (min-width:$mobile) {
        grid-column: 5 / -1;
        padding-right: rem(20);
      }

      @media (min-width:$tablet) {
        padding-right: 0;
        grid-column: 7 / 10;
      }
    }
  }

  &__title {
    font-family: $fontSecond;
    color: #000;
    font-size: rem(36);
    line-height: 1.5;
    text-transform: uppercase;
    letter-spacing: rem(5.58);
  }

  &__text {
    color: #000;
    line-height: 1.5;
    margin-bottom: rem(30);
  }

  &__subtitle {
    color: #000;
    font-size: rem(24);
    line-height: 1.5;
    margin-bottom: rem(40);
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: rem(34);
    }
  }

  &__lable {
    color: rgba(0, 0, 0, 0.39);
    font-size: rem(24);
    line-height: 1.5;
    margin-bottom: rem(4);
  }

  &__link {
    padding: rem(4);
    margin: rem(-4);
    color: #000;
    line-height: 1.5;
    transition: all 0.3s;

    @media (any-hover: hover) {
      &:hover {
        color: $turquoise;
      }
    }
  }
}
