.order {
  position: fixed;
  z-index: 7;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s;

  &__wrapper {
    position: relative;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: rem(20);
    padding: 0 rem(18);
    width: percent(296, 320);
    max-width: rem(830);
    margin: 0 auto;
    min-height: 100%;
    background-color: $white;
    opacity: 0;
    transform: perspective(rem(600)) translate(0,-100%) rotate(45deg);
    transition: all 0.8s;

    @media (min-width:$mobile) {
      grid-template-columns: repeat(6, 1fr);
    }

    @media (min-width:$tablet) {
      grid-template-columns: repeat(8, 1fr);
      column-gap: rem(30);
      padding-inline: rem(30);
    }
  }

  &._modal-open {
    opacity: 1;
    visibility: visible;

    .order__wrapper {
      opacity: 1;
      transform: perspective(0) translate(0,0) rotate(0deg);
    }
  }

  &__body {
    padding-bottom: rem(50);
    grid-column: span 2;

    @media (min-width:$mobile) {
      grid-column: 2 / span 4;
      margin: 0 rem(-20);
    }

    @media (min-width:$tablet) {
      grid-column: 2 / span 6;
      margin: 0;
    }
  }

  &__header {
    display: flex;
    justify-content: space-between;
    padding: rem(50) 0;
  }

  &__button-close {
    position: absolute;
    @include adaptiveValue("top", 30, 10);
    @include adaptiveValue("right", 40, 10);
  }
}
