.home {
  height: 100vh;
  background-image: none;
  background-repeat: no-repeat;

  @media (min-width:$mobile) {
    background-image: url("../images/home/bg.png");
    background-position: percent(1130, 768) 0;
  }

  @media (min-width:$tablet) {
    background-position: 0 0;
  }

  &__container {
    position: relative;
  }

  &__body {
    position: relative;
    @include grid;
    height: 100%;
    @include adaptiveValue("padding-top", 140, 70);

    @media (min-width:$mobileSmall) {
      padding-inline: rem(30);
      max-width: rem(1230);

      margin: 0 auto;
    }

    @media (max-width:$toMobile) {
      row-gap: rem(20);
    }
  }

  &__content {
    grid-column: span 2;
    order: 2;
    display: flex;
    flex-direction: column;

    @media (max-width:$mobileSmall) {
      padding-inline: rem(30);
    }

    @media (min-width:$mobile) {
      align-items: flex-end;
      grid-column: span 3;
      order: 0;
      // padding-bottom: rem(42);
    }

    @media (min-width:$tablet) {
      grid-column: span 6;
    }
  }

  &__title {
    font-family: $fontSecond;
    color: $turquoise;
    white-space: nowrap;
    @include adaptiveValue("font-size", 64, 36, 768, 320, 1);
    @include adaptiveValue("letter-spacing", 9.92, 5.58, 768, 320, 1);
    text-transform: uppercase;
    margin-bottom: rem(30);

    @media (max-width:$tablet) {
      margin-bottom: 0;
      align-self: flex-start;
    }

    @media (min-width:$mobile) {
      position: absolute;
      left: rem(30);
      z-index: 2;
    }

    @media (min-width: em(1024)) {
      position: initial;
    }

    @media (min-width:$tablet) {
      padding-right: rem(20);
      margin-bottom: rem(20);
    }

    & span {
      @extend %text-outline;
    }
  }

  &__text {
    color: $colorText;
    font-size: rem(14);
    font-family: $fontFamily;
    line-height: 1.5;
    @include adaptiveValue("margin-bottom", 50, 20);

    @media (min-width:$mobile) {
      transform: translateX(rem(54));
      padding-top: 244px;
      padding-right: rem(20);
    }

    @media (min-width: em(1024)) {
      transform: translateX(0);
      padding-top: 0;
    }

    & span {
      color: $grey;
      font-size: rem(26);
      font-family: $fontFamily;
      font-weight: 700;
    }
  }

  &__button {

    @media (min-width:$mobile) {
      transform: translateX(rem(44));
    }

    @media (min-width: em(1024)) {
      transform: translateX(0);
    }
  }

  &__image-wrapper {
    grid-column: span 2;
    order: 1;

    @media (min-width:$mobile) {
      grid-column: span 3;
      order: 0;
      padding-left: rem(34);
    }

    @media (min-width:$tablet) {
      grid-column: span 6;
      padding-left: rem(74);
    }
  }

  &__image {
    height: 0;
    padding-bottom: 100%;
    width: 100%;
    @include hoverImg;

    @media (max-width:$mobile) {
      padding-bottom: 51%;
    }
  }

  &__more-about {
    position: absolute;
    bottom: rem(20);
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: center;
    writing-mode: vertical-lr;
    max-width: em(18, 12);
    font-size: rem(12);
    line-height: 1.5;
    color: $turquoise;
    transition: color 0.3s;

    @media (max-width:$mobile) {
      right: rem(8);
    }

    @media (min-width:$mobile) {
      left: rem(28);
    }

    & span {
      text-transform: uppercase;
      margin-top: rem(4);
    }

    &::before {
      margin-top: rem(10);
      font-size: rem(44);
      transition: transform 0.3s;
    }

    @media (any-hover: hover) {
      &:hover {
        color: $turquoiseLight;

        &::before {
          transform: translateY(rem(20));
        }
      }
    }

    &:active {
      color: $turquoiseLight;
    }
  }
}
