// <SELECT>
// Сніппет (HTML): sel
.select {
  min-width: 0;
  position: relative;

  // .select__body
  &__body {
    position: relative;
  }

  // .select__title
  &__title {
    font-family: $fontFamily;
    text-align: left;
    color: #000;
    line-height: 1.5;
    cursor: pointer;
    width: 100%;
    padding: rem(2.5) rem(16);
    background-color: transparent;
    border-bottom: rem(1) solid #bdbdbd;

    &::before {
      content: "";
      display: none;
    }

    @media (any-hover: hover) {
      &:hover {

        .select__content,
        .select__value::after {
          color: $turquoise;
        }
      }
    }

    &:active {
      color: $turquoise;
    }
  }

  // .select__value
  &__value {
    display: flex;
    min-width: 0;
    align-items: center;
    justify-content: space-between;
    transition: color 0.3s ease 0s;

    &::after {
      @extend %ic;
      content: "\e903";
      display: block;
      font-size: rem(7);
      transition: all 0.3s ease 0s;
      color: $grey;

      ._select-open & {
        transform: rotate(-180deg);
      }
    }

    &._select-pseudo-label {
      &::before {
        content: attr(data-pseudo-label);
        opacity: 0.5;
      }
    }
  }

  // .select__content
  &__content {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    color: $colorLabel;
    transition: color 0.3s ease 0s;
  }

  // .select__text
  &__text {
    flex: 1 1 auto;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  &__input {
    width: 100%;
    background-color: transparent;
    height: 100%;
  }

  // .select__options
  &__options {
    color: #828282;
    position: absolute;
    top: calc(100% - rem(1));
    min-width: 100%;
    left: 0;
    background-color: $colorBg;
    border: rem(1) solid #bdbdbd;
    // padding: rem(10) 0;
  }

  // .select__scroll
  &__scroll {
    overflow-y: auto;
    overflow-x: hidden;
    // Максимальна висота
    max-height: rem(200);
    // padding: 0 0;

    // Стилі скроллбару
    // .simplebar-wrapper {
    //   border-radius: rem(4);
    // }

    .simplebar-track {
      background-color: transparent;
      max-width: rem(8);
      text-align: center;

      .simplebar-scrollbar {
        position: relative;

        &::before {
          position: absolute;
          right: 0;
          opacity: 1;
          background-color: #4d4d4d;
          width: rem(4);
          height: rem(32);
        }
      }
    }
  }

  // .select__option
  &__option {
    width: 100%;
    text-align: left;
    cursor: pointer;
    padding: rem(6) rem(16);
    color: inherit;
    background-color: transparent;
    transition: color 0.3s ease 0s;

    @media (any-hover: hover) {
      &:hover {
        background-color: #f2f2f2;
      }
    }

    &:active,
    &:focus {
      background-color: #f2f2f2;
    }
  }

  // Конструкція додаткових даних
  &__row {
    display: inline-flex;
    align-items: center;
  }

  &--month,
  &--year {

    & .select__options,
    & .select__content {
      color: #000;
    }
  }

  &--month {
    @media (min-width:$tablet) {
      margin-left: rem(-30);
    }
  }

  // Стан селекту
  &._select-open {
    z-index: 5;
  }
}

// Тег
._select-tag {
  cursor: pointer;
}
