.potr-size {

  &__body {
    padding-top: rem(100);
  }

  &__title {
    font-family: $fontSecond;
    color: #000;
    font-size: rem(36);
    line-height: 1.5;
    letter-spacing: rem(5.58);
    text-transform: uppercase;
    margin-bottom: rem(20);
    @include adaptiveValue("margin-bottom", 66, 30, 1230, 768, 0);
  }

  &__content {
    display: flex;
    @include adaptiveValue("column-gap", 94, 40, 1230, 768, 0);
    @include adaptiveValue("margin-bottom", 20, 14, 991, 320, 0);

    @media (max-width:$mobile) {
      flex-direction: column-reverse;
    }
  }

  &__description {
    flex: 0 1 percent(250, 708);

    @media (min-width:$tablet) {
      flex: 0 1 50%;
    }
  }

  &__main-info {
    display: flex;

    &:not(:last-child) {
      margin-bottom: rem(70);
    }
  }

  &__introduction {
    @include adaptiveValue("margin-bottom", 38, 20, 991, 320, 0);

    @media (min-width:$tablet) {
      flex: 0 1 50%;
      padding-right: rem(34);
    }
  }

  &__categories {
    @media (min-width:$tablet) {
      flex: 0 1 50%;
      border-left: rem(1) solid #d8d8d8;
      padding-left: rem(28);
      padding-bottom: rem(40);
    }
  }

  &__category {
    &:not(:last-child) {
      @include adaptiveValue("margin-bottom", 40, 16);
    }
  }

  &__sub-title {
    font-family: $fontSecond;
    font-size: rem(18);
    letter-spacing: rem(2.79);
    @include adaptiveValue("margin-bottom", 10, 4);
  }

  &__image--ibg-contain {
    flex: 0 1 percent(416, 708);

    &::after {
      content: "";
      display: block;
      padding-bottom: percent(427, 528);
    }

    @media (max-width:$mobile) {
      max-height: rem(337);

      margin-bottom: rem(20);
      width: 100vw;
      margin-left: calc(-50vw + 50%);
    }

    @media (min-width:$tablet) {
      flex: 0 1 50%;
    }

    @include hoverImg;
  }
}

.potr-size-text {
  color: $colorText;
  line-height: 1.5;
}
