.logo {
  display: inline-block;
  position: relative;
  z-index: 7;
  color: $grey;
  font-size: rem(36);
  font-family: $fontSecond;
  letter-spacing: rem(2.16);
}

.text-around {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: $fontSecond;
  font-size: rem(16);
  color: #cbcac8;
  text-transform: uppercase;
  @include square(rem(160));
  border-radius: 50%;

  &--questions {
    right: rem(-82);
    top: rem(-82);
    animation: rotation 13s infinite linear;

    @keyframes rotation {
      0% {
        transform: rotate(0deg);
      }

      100% {
        transform: rotate(360deg);
      }
    }

    @media (any-hover: hover) {
      &:hover {
        animation-play-state: paused;
      }
    }
  }

  & span {
    position: absolute;
    top: 0;
    letter-spacing: rem(-20);
    transform-origin: 0 rem(80);
  }
}

.icon-menu {
  display: block;
  position: relative;
  right: 0;
  z-index: 6;
  width: rem(30);
  height: rem(20);
  background-color: transparent;

  @media (any-hover: none) {
    cursor: default;
  }

  @media (min-width:$fromMobile) {
    display: none;
  }

  & span,
  &::before,
  &::after {
    content: "";
    position: absolute;
    // z-index: 5;
    height: rem(4);
    width: 100%;
    right: 0;
    background-color: #4d4d4d;
    transition: all 0.3s ease 0s;
  }

  & span {
    top: calc(50% - rem(2));
  }

  &::before {
    top: 0;
  }

  &::after {
    bottom: 0;
  }

  @media (any-hover: hover) {
    &:hover {

      & span,
      &::before,
      &::after {
        background-color: $turquoise;
      }
    }
  }

  &:active {

    & span,
    &::before,
    &::after {
      background-color: $turquoise;
    }
  }
}

.icon-close {
  display: none;

  @media (max-width:$toMobile) {
    display: inline-flex;
    align-items: center;
    position: absolute;
    bottom: rem(42);
    right: rem(32);
    z-index: 9;
    background-color: transparent;
    writing-mode: vertical-lr;
    transform: rotate(180deg);
  }

  &__text {
    position: relative;
    display: inline-flex;
    align-items: center;
    margin-top: rem(68);
    color: $colorBg;
    font-size: rem(12);
    font-family: $fontFamily;
    line-height: 1.5;
    text-transform: uppercase;
    transition: color 0.3s;

    &::after {
      content: "";
      position: absolute;
      top: -140%;
      height: rem(44);
      width: rem(1);
      background-color: $colorBg;
      transition: background-color 0.3s;
    }
  }

  &__cross {
    color: $colorBg;
    font-size: rem(14);
    transition: color 0.3s;
  }

  @media (any-hover: hover) {
    &:hover {

      .icon-close__text,
      .icon-close__cross {
        color: $yellowLight;
      }

      .icon-close__text::after {
        background-color: $yellowLight;
      }
    }
  }

  &:active {

    .icon-close__text,
    .icon-close__cross {
      color: $yellowLight;
    }

    .icon-close__text::after {
      background-color: $yellowLight;
    }
  }
}

.vertical-nav {
  position: absolute;
  display: inline-flex;
  flex-direction: row-reverse;
  align-items: center;
  writing-mode: vertical-lr;
  width: rem(30);
  height: max-content;
  color: $colorLabel;
  font-size: rem(12);
  padding: rem(4);
  font-family: $fontSecond;
  letter-spacing: rem(2.64);
  text-transform: uppercase;
  transition: all 0.3s;

  &::after {
    content: "";
    display: inline-block;
    width: rem(1);
    height: rem(65);
    margin-bottom: rem(10);
    background-color: $colorLabel;
    transition: all 0.3s;
  }

  @media (any-hover: hover) {
    &:hover {
      color: $turquoise;

      &::after {
        transform: translateY(rem(-20));
        background-color: $turquoise;
      }
    }
  }
}

.title-footer {
  font-family: $fontSecond;
  font-size: rem(24);
  color: #000;
  letter-spacing: rem(3.72);
  text-transform: uppercase;
}

.footer-text {
  color: #000;
  line-height: 1.5;
}

.footer-wrapper {
  background: url("../images/question/bg-m.png") 0 0 / auto 95% no-repeat;

  @media (max-width:$mobileSmall) {
    background-size: auto 88%;
  }

  @media (min-width:$mobile) {
    background-image: url("../images/question/bg.png");
  }
}

.modal-title {
  font-size: rem(36);
  font-family: $fontSecond;
  color: #000;
  line-height: 1.5;
  letter-spacing: rem(5.58);
  text-transform: uppercase;
}
