.footer {
  position: relative;

  &__body {
    display: flex;
    justify-content: space-between;
    padding: rem(64) 0;
    @include adaptiveValue("padding-top", 64, 30);
    @include adaptiveValue("padding-bottom", 64, 30);

    @media (max-width:$mobile) {
      justify-content: center;
    }
  }

  &__address {
    display: none;

    @media (min-width:$mobile) {
      display: block;
    }
  }

  &__address-title {
    margin-bottom: rem(10);
  }

  &__address-text {
    transition: all 0.3s;

    @media (any-hover: hover) {
      &:hover {
        color: $turquoise;
      }
    }
  }

  &__action {
    padding: 0 rem(14);
    text-align: center;
  }

  &__socials {
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-width: rem(232);
    margin-bottom: rem(74);
  }

  &__social-link {
    display: inline-flex;
    font-size: rem(20);
    transition: all 0.3s;

    &._icon-instagram {
      font-size: rem(26);
    }

    @media (any-hover: hover) {
      &:hover {
        transform: scale(1.1);
        color: $turquoise;
      }
    }
  }

  &__logo {
    font-family: $fontSecond;
    font-size: rem(36);
    color: #000;
    line-height: 1.5;
    letter-spacing: rem(5.58);
    text-transform: uppercase;
  }

  &__about {
    display: none;

    @media (min-width:$mobile) {
      display: block;
    }
  }

  &__about-title {
    margin-bottom: rem(10);
  }
}
