.form {
  &__fields {
    @include adaptiveValue("margin-bottom", 80, 60);

    &--address {
      @media (min-width:$mobileSmall) {
        display: grid;
        column-gap: rem(20);
        grid-template-columns: repeat(2, 1fr);
      }

      @media (min-width:$tablet) {
        column-gap: rem(30);
      }
    }

    &--payment {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      align-items: baseline;
      column-gap: rem(20);

      @media (min-width:$mobile) {
        grid-template-columns: repeat(4, 1fr);
      }

      @media (min-width:$tablet) {
        grid-template-columns: repeat(6, 1fr);
        column-gap: rem(30);
      }
    }
  }

  &__step-name {
    grid-column: span 2;
    position: relative;
    color: #000;
    line-height: 1.5;
    @include adaptiveValue("margin-bottom", 40, 30, 1230, 768, 1);

    @media (max-width:$mobile) {
      padding-left: rem(44);
    }

    & .circle {
      position: absolute;
      top: 0;
      left: 0;
      @include square(rem(24));
      border-radius: 50%;
      background-color: $grey;

      &::before {
        content: "";
        position: absolute;
        top: 55%;
        left: 53%;
        transform: translate(-50%, -50%);
        color: $white;
        font-size: rem(12);
        font-family: $fontSecond;
        letter-spacing: rem(1.8);
      }

      @media (min-width:$mobile) {
        left: rem(-44);
      }

      @media (min-width:$tablet) {
        left: rem(-54);
      }

      @for $i from 1 through 3 {
        &--#{$i} {
          &::before {
            content: "#{$i}";
          }
        }
      }
    }

    &--3 {
      grid-column: 1 / -1;
    }
  }

  &__item {
    position: relative;

    &:not(:last-child) {
      @include adaptiveValue("margin-bottom", 30, 20);
    }

    &--email {
      grid-column: span 2;
    }

    &--payment {
      grid-column: 1 / span 2;

      @media (min-width:$mobile) {
        grid-column: 2 / span 3;
      }

      @media (min-width:$tablet) {
        grid-column: 3 / span 4;
        margin-left: rem(-30);
      }
    }

    &--selects {
      grid-column: 1 / span 2;
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      column-gap: rem(20);

      @media (min-width:$mobile) {
        grid-column: 2 / span 3;
      }

      @media (min-width:$tablet) {
        grid-column: 3 / span 4;
        column-gap: rem(30);
      }
    }

    &:has(.form__input--cvc) {
      grid-column: 2 / -1;

      @media (max-width:$mobileSmall) {
        margin-left: rem(-50);
      }

      @media (min-width:$mobile) {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        column-gap: rem(20);
      }

      @media (min-width:$tablet) {
        grid-column: 3 / -1;
        margin-left: rem(-30);
        column-gap: 0;
      }
    }
  }

  input:-webkit-autofill,
  input:-webkit-autofill:hover,
  input:-webkit-autofill:focus,
  textarea:-webkit-autofill,
  textarea:-webkit-autofill:hover,
  textarea:-webkit-autofill:focus,
  select:-webkit-autofill,
  select:-webkit-autofill:hover,
  select:-webkit-autofill:focus {
    -webkit-text-fill-color: $turquoise;
    -webkit-box-shadow: 0 0 0 1000px transparent inset;
    transition: background-color 36000s linear 0s;
  }

  &__label {
    position: absolute;
    bottom: 0;
    padding: rem(4) rem(16);
    color: $colorLabel;
    cursor: text;

    &--textarea {
      bottom: 0;
    }
  }

  &__input {
    display: block;
    width: 100%;
    height: 100%;
    padding: rem(4) rem(16);
    font-family: $fontFamily;
    font-size: rem(16);
    line-height: 1.5;
    background-color: transparent;
    border-bottom: rem(1) solid #bdbdbd;
    outline: none;

    &::-webkit-outer-spin-button,
    &::-webkit-inner-spin-button {
      -webkit-appearance: none;
      // margin: 0;
    }

    &:focus {
      border-color: $turquoise;

      & + .form__label {
        opacity: 0;
        visibility: hidden;
      }
    }

    &._complete {
      color: $turquoise;
      border-color: $turquoise;

      & + .form__label {
        opacity: 0;
        visibility: hidden;
      }
    }

    &._error {
      color: $error;
      border-color: $error;

      & + .form__label {
        color: $error;
      }

      // &::placeholder {
      //   color: #860404;
      // }
    }

    &--textarea {
      resize: none;
      min-height: rem(63);
    }
  }

  &__payment-method {
    display: flex;
    align-items: center;
    column-gap: rem(14);
    padding: rem(10) 0;
    color: $colorLabel;
    border-bottom: rem(1) solid #bdbdbd;
    cursor: pointer;

    &:not(:last-child) {
      @include adaptiveValue("margin-bottom", 28, 20);
    }

    @media (min-width:$mobile) {
      padding-inline: rem(16);
    }
  }

  &__icon {
    display: flex;
  }

  &__icon-card {

    &:not(:last-child) {
      margin-right: rem(8);
    }
  }

  &__custom-radio {
    position: relative;
    flex-shrink: 0;
    flex-grow: 0;
    display: inline-block;
    @include circle(rem(14));
    border: 1px solid $colorLabel;

    &::before {
      content: "";
      position: absolute;
      left: 50%;
      top: 50%;
      @include circle(rem(8));
      background-color: $grey;
      transform: scale(0);
      transition: all 0.3s;
    }
  }

  &__credit-cards,
  &__pay-pal {
    display: none;

    &:checked + .form__custom-radio::before {
      transform: translate(-50%, -50%) scale(1);
    }
  }

  &__pay-pal {
    margin-right: rem(10);
  }

  &__amount {
    grid-column: 1 / -1;
    display: flex;
    margin-bottom: rem(30);
  }

  &__amount-text {
    @include adaptiveValue("margin-right", 110, 86);
  }

  &__item-label {
    grid-column: 1 / span 2;
    line-height: 1.5;
    color: #000;

    @media (max-width:$mobile) {
      &:not(:last-child, .form__item-label--cvc) {
        margin-bottom: rem(12);
      }
    }

    @media (min-width:$mobile) {
      white-space: nowrap;
      grid-column: 1 / span 1;
    }

    @media (min-width:$tablet) {
      grid-column: 1 / span 2;
    }

    &--cvc {
      grid-column: 1 / 2;
    }
  }

  &__error {
    font-size: rem(14);
    line-height: math.div(21, 14);
    color: $error;
  }

  &__button {
    @media (max-width: em(679.98)) {
      width: 100%;
    }
  }
}

