.payment-completed {
  position: fixed;
  z-index: 7;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s;

  &__wrapper {
    width: percent(296,320);
    max-width: rem(830);
    margin: 0 auto;
  }

  &__body {
    background-color: $white;
    color: #000;
    margin-top: rem(200);
    padding-top: rem(10);
    padding-inline: rem(80);
    @include adaptiveValue("padding-left", 80, 20, 768, 320, 1);
    @include adaptiveValue("padding-right", 80, 20, 768, 320, 1);
    @include adaptiveValue("padding-bottom", 170, 30);
    opacity: 0;
    transform: perspective(rem(600)) translate(0,-100%) rotate(45deg);
    transition: all 0.8s;
  }

  &._modal-open {
    opacity: 1;
    visibility: visible;

    .payment-completed__body {
      opacity: 1;
      transform: perspective(0) translate(0,0) rotate(0deg);
    }
  }

  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: rem(70) 0;
  }

  &__logo {
    position: relative;
    z-index: 1;
    padding-left: rem(48);
    font-size: rem(18);
    font-family: $fontSecond;
    letter-spacing: rem(2.67);
    text-transform: uppercase;

    &::before {
      content: "";
      @include circle(rem(80));
      position: absolute;
      z-index: -1;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      background-color: #ffe339;
    }
  }

  &__title {
    @include adaptiveValue("font-size", 32, 24);
    font-weight: 700;
    line-height: 1.5;
    margin-bottom: rem(16);
  }

  &__text {
    line-height: 1.5;

    &:not(:last-child) {
      margin-bottom: rem(10);
    }
  }
}
