.header {
  position: relative;
  z-index: 6;

  &__wrapper {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
  }

  &._scrolling {
    .header__wrapper {
      background-color: rgba(247, 247, 247, 0.8);
    }
  }

  &__body {
    display: flex;
    align-items: center;
    padding: rem(10) 0;
    @include adaptiveValue("min-height", 78, 60);
    justify-content: space-between;
  }

  &__logo {
    @media (min-width:$tablet) {
      flex: 0 1 rem(400);
    }
  }

  &__menu {
    @media (min-width:$tablet) {
      flex: 0 0 auto;
    }
  }

  &__basket-wrapper {
    text-align: right;

    @media (min-width:$tablet) {
      flex: 0 1 rem(300);
    }
  }

  &__basket {
    display: none;

    @media (min-width:$fromMobile) {
      position: relative;
      z-index: 6;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      background-color: transparent;
      padding: rem(10);
      font-size: rem(30);
      color: #434240;
      transition: color 0.3s ease 0s;
    }

    @media (any-hover: hover) {
      &:hover {
        color: $yellowLight;
      }
    }
  }
}

.menu {
  &__body {
    @media (max-width:em(768)) {
      position: fixed;
      z-index: 7;
      right: -100%;
      height: 100%;
      top: 0;
      width: percent(230, 320);
      max-height: 100%;
      padding: rem(140) rem(60) rem(30) rem(70);
      background-color: $turquoise;
      overflow-y: auto;
      text-align: center;
      transition: all 0.3s;

      &::before {
        content: "";
        position: fixed;
        z-index: 8;
        right: -100%;
        width: percent(230, 320);
        top: 0;
        @include adaptiveValue("height", 80, 70);
        background-color: $turquoise;
        transition: all 0.3s;
      }

      &._menu-open {
        right: 0;

        &::before {
          right: 0;
        }
      }
    }
  }

  &__list {
    display: inline-flex;
    flex-direction: column;
    align-items: baseline;

    @media (min-width:$fromMobile) {
      position: relative;
      z-index: 6;
      flex-direction: row;
      align-items: center;
      flex-wrap: wrap;
      @include adaptiveValue("column-gap", 60, 40, 1490, 768, 1);
      row-gap: rem(5);
    }
  }

  &__item {
    &--basket {
      display: block;
      position: relative;

      & span {
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: -3%;
        right: 0;
        border-radius: 50%;
        width: rem(14);
        height: rem(14);
        background-color: $error;
        font-size: rem(11);
        transform: translateX(rem(21));
        color: $colorBg;
      }

      @media (min-width:$fromMobile) {
        display: none;
      }
    }

    @media (max-width:$toMobile) {
      &:not(:last-child) {
        margin-bottom: rem(15);
      }
    }
  }

  &__link {
    display: block;
    color: $colorText;
    line-height: 1.5;
    transition: color 0.3s ease 0s;

    @media (max-width:$toMobile) {
      color: $colorBg;
    }

    @media (any-hover: hover) {
      &:hover {
        @media (max-width:$toMobile) {
          color: $yellowLight;
        }

        color: $turquoise;
      }
    }
  }
}
